class Game {
  constructor($el) {
    this.el = $el;
    this.setupGame();
  }

  button() {
    let that = this;
    let button = $('button');
    console.log(button);
    $('button').click((e) => {
      e.preventDefault();
      $('.sky').css("justify-content", "space-between");
    });
  }

  cssField() {
    let cssField = $('textarea');
    cssField.on('keyup', () => {
      let styles = cssField.val().split(':');
      if (styles[1]) {
        styles[1] = styles[1].slice(0, styles[1].length - 1 ).trim();
      }
      styles.map((style) => {
        style.trim();
      });

      console.log(styles[0], styles[1]);
      $('.sky').css(styles[0], styles[1]);
    });
  }

  setupGame() {
    for (let i = 0; i < 2; i++) {
      let frog = $(`<div>frog</div>`);
      this.el.append(frog);
    }
    this.button();
    this.cssField();
  }
}

export default Game;



